@charset "UTF-8";
:root {
  --text-color: #7F8082;
  --text-color-dark: #303030;
  --accent-color: #EF7F1A;
  --accent-color-hover: #FF971D;
  --accent-color2: #337ab7;
  --accent-color2-hover: #004b88;
  --stroke: #E7E7E7;
  --bg: #F5F5F7;
  --box-shadow: 0 3px 10px rgba(127, 128, 130, 0.1), 0 15px 25px rgba(127, 128, 130, 0.15);
  --box-shadow-btn: 0 3px 10px rgba(239, 127, 26, 0.2), 0 15px 25px rgba(239, 127, 26, 0.25);
  --orange_color: #EF7F1A;
  --red_color: #d9534f;
  --red_color2: #ff5400;
}

.breadcrumb ol, .menu-top nav ul, .header__nav ul, .ul-custom ul, .text-elements ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

html {
  font-size: 16px;
}

body {
  font-family: "Lato-my", "Lato-my Fallback";
  color: var(--text-color);
  line-height: 1.4;
  min-width: 320px;
  position: relative;
}
@media only screen and (max-width: 767px) {
  body {
    font-size: 0.875rem;
  }
}

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.page__offset-top {
  margin-top: 40px;
}
@media only screen and (max-width: 991px) {
  .page__offset-top {
    margin-top: 30px;
  }
}
@media only screen and (max-width: 575px) {
  .page__offset-top {
    margin-top: 20px;
  }
}

.main {
  flex: 1 0 auto;
}

.page__two-columns {
  flex: 1 0 auto;
  display: grid;
  grid-template-areas: "sections-top sections-top" "aside main" "sections-bottom sections-bottom";
  gap: 0 30px;
  grid-template-columns: 300px 1fr;
  grid-template-rows: auto auto 1fr;
}
@media only screen and (max-width: 1359px) {
  .page__two-columns {
    grid-template-columns: 210px 1fr;
  }
}
@media only screen and (max-width: 991px) {
  .page__two-columns {
    grid-template-columns: 1fr;
    grid-template-areas: "sections-top" "main" "sections-bottom" "aside";
  }
}
.page__two-columns .main {
  flex: none;
  grid-area: main;
}

.aside {
  grid-area: aside;
}
@media only screen and (max-width: 991px) {
  .aside {
    display: none;
  }
}
.aside > div:not(:last-child) {
  margin-bottom: 30px;
}

.page__sections-top {
  grid-area: sections-top;
}

.page__sections-bottom {
  grid-area: sections-bottom;
}

.page__main-column .container,
.page__main-column .container-fluid,
.aside .container,
.aside .container-fluid,
.container:not(.container-wrapper) .container,
.container:not(.container-wrapper) .container-fluid {
  max-width: 100%;
  padding-left: 0;
  padding-right: 0;
  margin-left: inherit;
  margin-right: inherit;
}

.widgets-right {
  position: absolute;
  top: 0;
  right: 0;
  width: calc((100% - 1320px) / 2);
  height: 100%;
}
@media only screen and (max-width: 1359px) {
  .widgets-right {
    width: calc((100% - 960px) / 2);
  }
}
@media only screen and (max-width: 991px) {
  .widgets-right {
    width: calc((100% - 720px) / 2);
  }
}
@media only screen and (max-width: 767px) {
  .widgets-right {
    display: none;
  }
}

@media only screen and (max-width: 575px) {
  .row {
    margin-left: -7.5px;
    margin-right: -7.5px;
  }
  .row > .col,
  .row > [class*=col-],
  .row > [class*=col15-] {
    padding-left: 7.5px;
    padding-right: 7.5px;
  }
}
.row-mini {
  margin-left: -6px;
  margin-right: -6px;
}
.row-mini > .col,
.row-mini > [class*=col-],
.row-mini > [class*=col15-] {
  padding-left: 6px;
  padding-right: 6px;
}

.row-mini15 {
  margin-left: -7.5px;
  margin-right: -7.5px;
}
.row-mini15 > .col,
.row-mini15 > [class*=col-],
.row-mini15 > [class*=col15-] {
  padding-left: 7.5px;
  padding-right: 7.5px;
}

.row-mini20 {
  margin-left: -10px;
  margin-right: -10px;
}
.row-mini20 > .col,
.row-mini20 > [class*=col-],
.row-mini20 > [class*=col15-] {
  padding-left: 10px;
  padding-right: 10px;
}

.mb-30:not(:last-child),
.tpl-block-list-objects:not(:last-child) {
  margin-bottom: 30px;
}
@media only screen and (max-width: 575px) {
  .mb-30:not(:last-child),
  .tpl-block-list-objects:not(:last-child) {
    margin-bottom: 15px;
  }
}

.mb-15:not(:last-child) {
  margin-bottom: 15px;
}

.gap-30 {
  gap: 30px 0;
}
@media only screen and (max-width: 575px) {
  .gap-30 {
    gap: 15px 0;
  }
}

.gap-15 {
  gap: 15px 0;
}

