/*==========  HEADER MOBILE  ==========*/
.header__mobile{
  display: none;
}
@media only screen and (max-width: 767px) {
  .header{
    position: sticky;
    top: 0;
    z-index: 10;
  }

  .header__mobile{
    display: grid;
    grid-template-columns: 40px auto 40px;
    gap: 5px 10px;
    align-items: center;
    justify-content: space-between;
    min-height: 50px;
    color: rgba(0,0,0,.4);
    background: #fff;
    box-shadow: var(--box-shadow);
    padding-right: 5px;
  }

  //.header__mobile-buttons{
  //height: inherit;
  //display: flex;
  //a{
  //  display: flex;
  //  justify-content: center;
  //  align-items: center;
  //  text-align: center;
  //
  //  text-decoration: none;
  //  width: 40px;
  //  height: inherit;
  //  color: inherit;
  //  &.active{
  //    background: rgba(255,255,255,.4);
  //  }
  //}
  //}

  @import '../../helpers/mburger';
  .mburger {
    grid-row: 1 / 3;
    height: 100%;

    --mb-button-size: 40px;
    --mb-bar-width: 0.6;
    --mb-bar-height: 2px;
    --mb-bar-spacing: 6px;
    .mm-wrapper_opened &{
      background: rgba(255,255,255,.4);
    }
  }

  .header__mobile-logo {
    padding-top: 5px;
    display: grid;
    grid-template-columns: 44px auto;
    gap: 3px 10px;

    text-decoration: none;
    font-family: "Montserrat-my", sans-serif;
    text-transform: uppercase;
  }

  .header__mobile-logo-img{
    grid-row: span 2;
  }

  .header__mobile-logo-title {
    grid-column: 2;
    font-weight: 700;
    font-size: 15px;
    line-height: 1;
    color: var(--accent-color);

    overflow: hidden;
    text-overflow: ellipsis;
  }

  .header__mobile-logo-text {
    font-size: 12px;
    letter-spacing: 0.05em;
    line-height: 110%;
    color: var(--text-color);

    overflow: hidden;
    text-overflow: ellipsis;
  }

  .header__mobile-button{
    min-height: 30px;
    margin-bottom: 5px;
    box-shadow: none;
    width: 214px;
    @media only screen and (min-width: 375px) {
      margin-left: 55px;
      width: 200px;
    }
    &:active {
      box-shadow: none;
    }
  }

  .header__mobile-button-container{
    display: none;
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    padding: 10px 15px;
    background: inherit;
  }

  .header__mobile-search-container{
  }
}

// соцсети в моб. меню
.mm-navbar {
  .fa, .fab, .fas {
    //transform: translate3d(0, 0, 0); // чтобы иконки были четкими
    &.fa-vk{
      color: #45668e;
    }
    &.fa-facebook{
      color: #3b5998;
    }
    &.fa-twitter{
      color: #1da1f2;
    }
    &.fa-youtube{
      color: #ff0000;
    }
    &.fa-skype{
      color: #00AFF0;
    }
    &.fa-instagram:before{
      color: transparent;
      background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
      background-clip: text;
      -webkit-background-clip: text;
      line-height: 1;
    }
    &.fa-odnoklassniki{
      color: #ed812b;
    }
    &.fa-whatsapp{
      color: #075e54;
    }
    &.fa-viber{
      color: #675CA8;
    }
    &.fa-telegram-plane{
      color: #0088cc;
    }

  }
}

